.section-parallax {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 60vh;
  margin-left: 20vw;
  margin-bottom: 25vh;

  .parallax-left, .parallax-right {
    width: 30%;
    height: 100%;
    object-fit: cover;
    overflow: hidden;

    img { 
      position: relative;
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }

  .parallax-right {
    margin-left: 15%;
  }
}